import * as skype from '@skype/spool-sdk';
import { IHostCommand } from 'vscode-web';
import { ICallingService, IParticipantInfo, CallChanged,
    CallChangedType, ICallInfo, SkypeTokenInfo, CallingServiceProxyCommands } from '../src/ICallingService';
import { ICallClient, IDeviceManager, IDevice, ISelectedDevices,
    CPaaSOptions, CPaaSClient, ICall, CallState } from '@skype/spool-sdk';

export class CallingService implements ICallingService {
    private readonly logPrefix = '[CallingService]';
    private callClient: ICallClient | undefined;
    private deviceManager: IDeviceManager | undefined;
    private call: skype.ICall | undefined;
    private callParticipants = new Map<string, skype.ICallRemoteParticipant>();

    constructor(private vscode: any, private trace: any) {
    }

    public getCommands(): IHostCommand[] {
        return [
            {
                id: CallingServiceProxyCommands.startOrJoinCall,
                handler: async(groupContext: string, displayName: string, skypeTokenInfo: SkypeTokenInfo): Promise<void> =>
                    await this.startOrJoinCall(groupContext, displayName, skypeTokenInfo)
            },
            {
                id: CallingServiceProxyCommands.getCallInfo,
                handler: async (): Promise<ICallInfo | undefined> =>
                    this.getCallInfo()
            },
            {
                id: CallingServiceProxyCommands.getDevices,
                handler: async(): Promise<IDevice[]> =>
                    await this.getDevices()
            },
            {
                id: CallingServiceProxyCommands.getSelectedDevices,
                handler: async(): Promise<ISelectedDevices> =>
                    await this.getSelectedDevices()
            },
            {
                id: CallingServiceProxyCommands.selectDevices,
                handler: async(selectedDevices: ISelectedDevices): Promise<void> =>
                    await this.selectDevices(selectedDevices)
            },
            {
                id: CallingServiceProxyCommands.getParticipants,
                handler: async(): Promise<IParticipantInfo[]> =>
                    this.getParticipants()
            },
            {
                id: CallingServiceProxyCommands.endCall,
                handler: async(): Promise<void> =>
                    await this.endCall()
            },
            {
                id: CallingServiceProxyCommands.isMuted,
                handler: async(): Promise<boolean> =>
                    this.isMuted()
            },
            {
                id: CallingServiceProxyCommands.mute,
                handler: async(participantInfo?: IParticipantInfo): Promise<void> =>
                    await this.mute(participantInfo)
            },
            {
                id: CallingServiceProxyCommands.unmute,
                handler: async(participantInfo?: IParticipantInfo): Promise<void> =>
                    await this.unmute(participantInfo)
            }
        ];
    }

    public async startOrJoinCall(groupContext: string, displayName: string, skypeTokenInfo: SkypeTokenInfo): Promise<void> {
        if (this.call) {
            this.trace.warning(`${this.logPrefix} Already connected to a call`);
            await this.endCall();
            throw new Error('Already connected to a call. Ending current call. Please retry audio join');
        }

        try {
            if (!this.callClient) {
                this.trace.info(`${this.logPrefix} Initializing call client`);
                const options: CPaaSOptions = {
                    config: {
                        credentialProvider: () => {
                            return {
                                serviceToken: {
                                    token: skypeTokenInfo.skypeToken,
                                    expiration: skypeTokenInfo.expiresIn.toString()
                                },
                                mri: skypeTokenInfo.skypeId,
                                // skyoe resourceId isn't being consumed at this time
                                resourceId: 'resourceId'
                            };
                        },
                        userDisplayName: displayName
                    },
                    options: {}
                };
                const cPaasClient= new CPaaSClient(options);
                this.callClient = await cPaasClient.getCallClient();
                this.deviceManager = this.callClient.deviceManager;
            }
            this.trace.info(`${this.logPrefix} Connecting to call with group context: ${groupContext}`);
            const call = await this.callClient.joinGroup( { groupId: groupContext } );
            await this.addCallingEvents(call);
            this.call = call;
        } catch (e) {
            this.trace.error(`${this.logPrefix} error while connecting to call: ${e.message}`);
            throw e;
        }
    }

    public async endCall(): Promise<void> {
        this.trace.info(`${this.logPrefix} endAndCleanUpCurrentCall started`);

        if (!this.call) {
            return;
        }

        try {
            await this.call.endCall();
        }
        finally {
            this.callParticipants.clear();
            this.call?.removeAllListeners();
            this.call = undefined;
        }
        this.trace.info(`${this.logPrefix} endAndCleanUpCurrentCall completed`);
    }

    public async getDevices(): Promise<IDevice[]> {
        if (!this.deviceManager) {
            return [];
        }
        const devices = await this.deviceManager.getDevices();
        this.trace.info(`${this.logPrefix} finished enumerating devices: ${devices}`);
        return devices;
    }

    public async getSelectedDevices(): Promise<ISelectedDevices> {
        if (!this.deviceManager) {
            return {};
        }
        return await this.deviceManager.getSelectedDevices();
    }

    public async getParticipants(): Promise<IParticipantInfo[]> {
       return Array.from(this.callParticipants, ([key, { displayName, userId, isMuted, isSpeaking }]) => {
           return {
               displayName: displayName,
               id: userId,
               isMuted: isMuted,
               isSpeaking: isSpeaking
            };
        });
    }

    public async selectDevices(selectedDevices: ISelectedDevices): Promise<void> {
        if (!this.deviceManager) {
            return;
        }
        await this.deviceManager.setSelectedDevices(selectedDevices);
    }

    public async isMuted(): Promise<boolean> {
        if (!this.call) {
            return true;
        }
        return this.call.isMicrophoneMuted;
    }

    public async mute(participantInfo?: IParticipantInfo): Promise<void> {
        if (!this.call) {
            return;
        }

        if (!participantInfo) {
              await this.call.muteMicrophone();
        }
    }

    public async unmute(participantInfo?: IParticipantInfo): Promise<void> {
        if (!this.call) {
            return;
        }

        if (!participantInfo) {
            await this.call.unmuteMicrophone();
        }
    }

    public async getCallInfo(): Promise<ICallInfo | undefined> {
        if (!this.call) {
            return;
        }

        return {
            callId: this.call.callId,
            state: this.call.state
        };
    }

    public async dispose() {
        await this.endCall();
        this.callClient?.dispose();
        this.deviceManager = undefined;
    }

    private async addCallingEvents(call: ICall) {
        if (!call) {
            return;
        }

        call.on('stateChanged', async(state: CallState) => {
            this.trace.info(`${this.logPrefix} stateChanged: ${state}`);

            switch (call.state) {
                case 'Connected':
                    {
                        this.trace.info(`${this.logPrefix} callStateChanged: connected to call`);
                        const callInfo = {
                                callId: call.callId,
                                state: call.state
                        };
                        const event = new CallChanged(CallChangedType.callStarted, callInfo);
                        await this.vscode.commands.executeCommand(CallingServiceProxyCommands.onCallChangedEvent, event);
                    }
                    break;
                case 'Disconnected':
                    const event = new CallChanged(CallChangedType.callDisconnected);
                    await this.vscode.commands.executeCommand(CallingServiceProxyCommands.onCallChangedEvent, event);
                    await this.endCall();
                    break;
                default:
                    return;
            }
        });

        call.on('participantAdded', async (participant): Promise<void> => {
            this.trace.info(`${this.logPrefix} participantAdded: ${participant.userId}`);
            this.callParticipants.set(participant.userId, participant);

            participant.addListener('isMutedChanged', (isMuted: boolean) => {
                this.trace.info(`${this.logPrefix} isMuted: ${isMuted}`);
                this.sendParticipantChangedEvent(participant);
            });

            participant.addListener('isSpeakingChanged', (isSpeaking) => {
                this.trace.info(`${this.logPrefix} isSpeaking: ${isSpeaking}`);
                this.sendParticipantChangedEvent(participant);
            });

            const event = new CallChanged(CallChangedType.participantsListChanged);
            await this.vscode.commands.executeCommand(CallingServiceProxyCommands.onCallChangedEvent, event);
        });

        call.on('participantRemoved', async (participant): Promise<void> => {
            this.trace.info(`${this.logPrefix} participantRemoved: ${participant.userId}`);
            participant.removeAllListeners();
            this.callParticipants.delete(participant.userId);
            const event = new CallChanged(CallChangedType.participantsListChanged);
            await this.vscode.commands.executeCommand(CallingServiceProxyCommands.onCallChangedEvent, event);
        });
    }

    private async sendParticipantChangedEvent(participant: skype.ICallRemoteParticipant): Promise<void> {
        const participantInfo = {
            displayName: participant.displayName,
            id: participant.userId,
            isMuted: participant.isMuted,
            isSpeaking: participant.isSpeaking,
        };
        const event = new CallChanged(CallChangedType.participantChanged, participantInfo);
        await this.vscode.commands.executeCommand(CallingServiceProxyCommands.onCallChangedEvent, event);
    }
}