export declare interface ISelectedDevices {
    camera?: string;
    microphone?: string;
    speaker?: string;
}

export declare interface IDevice {
    /**
     * Get device's name.
     */
    readonly name: string;
    /**
     * Get device's ID.
     */
    readonly id: string;
    /**
     * Get device type.
     */
    readonly type: DeviceType | undefined;
}

export declare type CallState = 'None' | 'Incoming' | 'Connecting' | 'Ringing' | 'Connected' | 'LocalHold' | 'RemoteHold' | 'Disconnecting' | 'Disconnected' | 'EarlyMedia';
export declare type DeviceType = 'Camera' | 'Microphone' | 'Speaker';


export interface IParticipantInfo {
    displayName: string;
    id: string;
    isMuted: boolean;
    isSpeaking: boolean;
}

export interface SkypeTokenInfo {
    skypeToken: string;
    expiresIn: number;
    skypeId: string;
}

export enum CallChangedType {
    callStarted,
    callDisconnected,
    participantsListChanged,
    participantChanged
}

export class CallChanged {
    constructor(public readonly changeType: CallChangedType,
                public readonly info?: ICallInfo | IParticipantInfo) { }
}

export interface ICallInfo {
    callId: string;
    state: CallState;
}

export enum CallingServiceProxyCommands {
    startOrJoinCall = '_liveshareweb.callingService.startOrJoinCall',
    getCallInfo = '_liveshareweb.callingService.getCallInfoCommand',
    getDevices = '_liveshareweb.callingService.getDevices',
    getSelectedDevices = '_liveshareweb.callingService.getSelectedDevices',
    selectDevices = '_liveshareweb.callingService.selectDevices',
    getParticipants = '_liveshareweb.callingService.getParticipants',
    endCall = '_liveshareweb.callingService.endCall',
    isMuted = '_liveshareweb.callingService.isMuted',
    mute = '_liveshareweb.callingService.mute',
    unmute = '_liveshareweb.callingService.unmute',
    onCallChangedEvent = '_liveshareweb.callingService.onCallChangedEvent'
}

export interface ICallingService {
    getCallInfo(): Promise<ICallInfo | undefined>;
    startOrJoinCall(groupContext: string, displayName: string, skypeTokenInfo: SkypeTokenInfo): Promise<void>;
    getDevices(): Promise<IDevice[]>;
    getSelectedDevices(): Promise<ISelectedDevices>;
    selectDevices(devices: ISelectedDevices): Promise<void>;
    getParticipants(): Promise<IParticipantInfo[]>;
    endCall(): Promise<void>;
    isMuted(): Promise<boolean>;
    mute(participantInfo?: IParticipantInfo): Promise<void>;
    unmute(participantInfo?: IParticipantInfo): Promise<void>;
}